<template>
  <!-- <div class="nav-wrapper">
    
  </div> -->
  <b-navbar
    toggleable="lg"
    type="light"
    sticky
  >
    <b-navbar-brand to="/">
      <img src="@/assets/logo.png" class="logo" />
    </b-navbar-brand>

    <b-navbar-toggle target="nav-collapse"></b-navbar-toggle>

    <b-collapse id="nav-collapse" is-nav>
      <b-navbar-nav>
        <b-nav-item
          v-for="(link, index) in links"
          :key="index"
          :to="link.url"
          :active="link.title === active"
          @click="active = link.title"
        >{{ link.title }}</b-nav-item>
      </b-navbar-nav>

      <!-- Right aligned nav items -->
      <b-navbar-nav class="ml-auto right-nav">
        <b-nav-item
          v-for="lang in langModes"
          :key="lang.value"
          :active="langMode === lang.value"
          @click="changeLangMode(lang.value)"
        >{{ lang.title }}</b-nav-item>
      </b-navbar-nav>
    </b-collapse>
  </b-navbar>
</template>
<script>
import { mapGetters } from "vuex"
export default {
  data() {
    return {
      active: '首页',
      links: [
        { url: '/', title: '首页' },
        { url: '/', title: '关于索智' },
        { url: '/', title: '产品管线' },
        { url: '/', title: '职位招聘' },
        { url: '/', title: '联系我们' }
      ],
      langModes: [
        { title: '中文', value: 'zh' },
        { title: 'English', value: 'en' }
      ]
    }
  },
  computed: {
    ...mapGetters(['langMode'])
  },
  methods: {
    changeLangMode(mode) {
      this.$store.dispatch('changeLangMode', mode)
    }
  }
}
</script>
